    const _ = require('lodash');
    import Velocity from 'velocity-animate'
    var done = ''
    import AxiosApi from '../../../api/axiosApi'
    const axioxapi = new AxiosApi()
    export default {
        data() {
            return {
                items:[],
                push_info:'',
                carry_info:[],
                show: false,

            }
        },
        props: {

        },
        created(){

            axioxapi.axios.get('//localhost:5001/livevideo/getvideolist')
                .then((res) => {
                    this.items = res.data

                })

        },
        mounted(){

        },
        updated(){
            $('tr').removeClass('onplay')
            $('tr:eq(1)').addClass('onplay')
        },
        methods:{

            push_click: function (target_user,target_movie,target_ticket) {

                let info = this.push_info?this.push_info:target_movie
                var select_els =  $('td:contains('+info+')')
                if(select_els !== undefined){
                    if(select_els.length === 1){

                        let el_rect = select_els[0].getBoundingClientRect()
                        if($('.ball-container').length  > 0){
                            $('.ball-container').css('height',el_rect.height)

                            var clone_ball = $('.ball-container').clone()
                            var target_ball_id =  Date.parse(new Date())+'-'+Math.random()
                            clone_ball.attr('target_ball_id',target_ball_id)
                            clone_ball.insertAfter($('.ball-container'))
                            var ball_target = $('div[target_ball_id="'+target_ball_id+'"]')

                            ball_target.removeClass('hidden')
                            ball_target.animate({top:el_rect.top}).animate({
                                top:el_rect.top
                            }).animate({
                                right:window.innerWidth-el_rect.width-30,
                                opacity:0.0
                            },{
                                duration:1500,
                                complete:function () {
                                    this.remove()
                                }
                            });
//


                        }


                    }
                }

            },
            sort_list: function () {
                this.items = _.sortBy(this.items, function(item) {
                    return -item.ticket;
                });

            },
            shuffle: function () {
                this.items = _.shuffle(this.items)
            },
            beforeEnter(el) {
                console.log('beforeEnter')
                el.style.opacity = 0
                el.style.height = 0
            },
            enter(el) {
                console.log('enter')
                var delay = el.dataset.index * 150
                setTimeout(function () {
                    Velocity(
                        el,
                        { opacity: 1, height: '1.6em' },
                        { complete: done }
                    )
                }, delay)
            },
            afterEnter(el) {
                console.log('afterenter')

//                let ball = this.dropBalls.shift()
//                if (ball) {
//                    ball.show = false
//                    el.style.display = 'none'
//                }
            },
            enterCancelled(el) {
                console.log('enterCancelled')

//                let ball = this.dropBalls.shift()
//                if (ball) {
//                    ball.show = false
//                    el.style.display = 'none'
//                }
            },
            beforeLeave(el){
                console.log('befaoreLeave')
            },
            leave(el){
                console.log('Leave')
                var delay = el.dataset.index * 150
                setTimeout(function () {
                    Velocity(
                        el,
                        { opacity: 0, height: 0 },
                        { complete: done }
                    )
                }, delay)
            },
            afterLeave(el){
                console.log('afterLeave')
            },
            leaveCancelled(el){
                console.log('leaveCancelled')
            },




        },
        computed:{
            computedList: function () {
                var vm = this
                return this.items.filter(function (item) {
                    return item.filename.toLowerCase().indexOf(vm.push_info.toLowerCase()) !== -1
                })
            }
        },
    }



