export default {

    data() {
        return {
            showBackdrop:false
        }
    },
    created(){
        $('.live_ctrl').width('100px')
        $('.live_ctrl').height('100px')
    },
    mounted(){

      $('#live_ctrl').on('dblclick',function () {
          $('#live_ctrl').toggleClass('pullright')
      })
    },
    methods:{
        hideBackdrop:function () {
            this.showBackdrop = !this.showBackdrop
        }
    }
}



